import { useAutorun } from '@speedlo/hooks'
import { Sentry } from '@speedlo/sentry'
import { useXFormCtx } from '@speedlo/xform'
import { debounce } from 'debounce'
import localforage from 'localforage'
import { when } from 'mobx'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { appConfig } from '../../config'
import { TOrderModel } from '../../models/OrderModel'
import { OrderFormValues } from './OrderFormSetup'

export const memoryKey = `${appConfig.persistenceKey}_orderForm`

const catchError = err => {
  Sentry.withScope(scope => {
    scope.setTag('persistence', memoryKey)
    Sentry.captureException(err)
  })
}

const writeMemory = debounce((values: OrderFormValues, done: () => void) => {
  localforage.setItem(memoryKey, {
    firstname: values.firstname,
    lastname: values.lastname,
    email: values.email,
    phonePrefix: values.phonePrefix,
    phone: values.phone,
    note: values.note,
  })
  done()
}, 3000)

const readMemory = (): Promise<any> => {
  return localforage.getItem(memoryKey).catch(catchError)
}

export const OrderFormMemory = React.memo(() => {
  const { user } = useRoot()
  const xform = useXFormCtx<OrderFormValues>()

  const applyMemory = React.useCallback(
    (memory: OrderFormValues) => {
      if (user.memoryApplied) {
        return
      }
      xform.api.mergeValues(memory)
      Object.entries(memory).forEach(([key, value]) => {
        if (value) {
          xform.state.touched.add(key)
        }
      })
      user.setMemoryApplied()
      user.log('applied order form memory')
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [user],
  )

  React.useEffect(() => {
    readMemory().then(memory => {
      if (memory) {
        applyMemory(memory)
        return
      }
      // legacy memory from OrderModel
      when(
        () => user.root.initialized,
        () => {
          applyMemory(getLegacyMemory(user.root.order))
        },
      )
    })
  }, [applyMemory, user])

  useAutorun(() => {
    if (!(user.shouldRememberForm && user.memoryApplied)) {
      return
    }
    writeMemory(xform.state.values, () => {
      user.log('written order form memory')
    })
  })

  return null
})

function getLegacyMemory(orderModel: TOrderModel) {
  orderModel.log('using legacy memory')
  const { firstname, lastname, email, phone, phonePrefix, note } = orderModel
  return {
    firstname,
    lastname,
    email,
    phone,
    phonePrefix,
    note,
    agreements: [],
    deliverAt: null,
  }
}
